@font-face {
  font-family: compote;
  src: url("fonts/Compote-Free.otf");
}

body {
  margin: 0;
  /* line-height: normal; */
  background-image: url("texturelabs-grunge-288m-2@2x.png");
  background-attachment: fixed;
  background-repeat: repeat;
  background-size: 100%;
  background-color: #949393;
  margin: 0px;
  position: relative;
  min-height: 100vh;
}

.heading {
  display: flex;
  justify-content: center;
}

.sub-heading {
  position: relative;
  /* display: flex; */
  /* flex-direction: column;
  justify-content: center;
  align-items: center; */
  /* background-image: url("public/group-28-1@2x.png"); */

  width: 50vw;
  height: 16vw;
}

.hText {
  /* all: unset; */
  position: relative;
  top: 4.5vw;
  left: 3vw;
  font-weight: 400;
  font-size: 7vw;
  font-family: compote;
  color: #2c2825;
}

.hImg {
  top: 2vw;
  height: 12vw;
  width: 50vw;
}

.btn-bucket {
  display: flex;
  width: auto;
  justify-content: center;
  /* top: 630px; */
  /* left: 579px; */
  font-size: 4vw;
}

.day {
  all: unset;
  padding: 4vw;
  font-weight: 700;
  font-family: Lato;
  opacity: 0.4;
  color: #f5f5dc;
}

.day:hover {
  cursor: pointer;
}

.events-container {
  position: relative;
  padding-bottom: 30px;
}

.event-tab {
  color: #2c2825;
  position: relative;
  display: block;
  /* top: 896px; */

  /* left: 8vw; */
  left: 15vw;
  /* width: 84vw; */
  width: 70vw;
  font-family: Lato;
  /* z-index: 300; */
  /* height: 261px; */
}
.event-bg {
  position: relative;
  /* display: block; */
  top: 0;
  left: 0;
  width: 70vw;
  height: 11vw;
  /* height: 234.81px; */
  /* object-fit: cover; */
}

.bg-bottom {
  position: absolute;
  top: 6.7vw;
  height: 10vw;
  left: 0px;
  width: 70vw;
}
.event-name {
  position: absolute;
  /* display: block; */
  font-weight: 700;
  top: 2.5vw;
  left: 5vw;
  font-size: 2.2vw;
  /* display: flex; */
  /* align-items: center; */
}
.venue-time {
  position: absolute;
  font-weight: 400;
  top: 6.2vw;
  font-size: 1.5vw;
  left: 5vw;
  /* width: 50vw; */
}

.line-vector {
  position: absolute;
  top: 5.5vw;
  left: 1vw;
  width: 60vw;
  /* height: 261px; */
}

a > .calendar {
  position: absolute;
  top: 5vw;
  right: 3.5vw;
}
img {
  position: absolute;
  width: 4vw;
}

.register {
  position: absolute;
  top: 9.3vw;
  font-size: 1.3vw;
  left: 5vw;
  color: #2c2825;
}

#footer {
  margin-top: 7.5vw;
  background-color: #161515;
  display: block;
}

#icons-box {
  display: block;
  padding-top: 2.5vw;
  padding-bottom: 6vw;
}
#icons {
  display: flex;
  justify-content: center;
}
.social {
  margin-left: 3vw;
  margin-right: 7vw;
}

#text-area {
  display: block;
  color: white;
  /* padding-top: 50px; */
  padding-bottom: 2.5vw;
}

#text {
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
  color: grey;
}
span {
  text-align: center;
  width: 30vw;
}

@media (max-width: 1100px) {
  .event-name {
    font-size: 3vw;
    top: 3vw;
  }
  .venue-time {
    font-size: 2.4vw;
    top: 8vw;
  }
  .line-vector {
    top: 7.5vw;
  }
  .register {
    font-size: 2vw;
    top: 11.6vw;
  }

  .event-bg {
    height: 14vw;
  }
  #footer {
    font-size: 2.2vw;
    position: absolute;
    width: 100%;
    bottom: 0;
  }

  span {
    width: 36vw;
  }

  .events-container {
    padding-bottom: 30vw;
  }
}
